nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background-color: #0e2430;
  color: #f5b349;
  font-family: 'IM Fell DW Pica';
}

.logo{
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 24px;
}

.links{
  display: flex;
  justify-content: space-around;
  width: 30%;
}

.links li{
  font-size: 14px;
  letter-spacing: 3px;
  font-weight: bolder;
  list-style: none;
}